import React from 'react'

import Image from '../../assets/ImageUpdate.svg'

const Responsabilities: React.FC = () => {
  return (
      <div className='w-full flex flex-col justify-evenly md:flex-row md:items-center'>
        <div className='w-auto md:w-4/5 lg:w-1/2 3xl:w-4/5'>
          <div
            className='
              mb-2
              text-xl
              sm:text-2xl
              font-semibold
              sm:pt-6
              md:text-3xl
              lg:text-3xl
              xl:text-4xl
              2xl:text-7xl
              3xl:text-7xl
              4xl:text-9xl'
            >
            <span className='leading-tight'>
              Nós te ajudamos a organizar seus exames online. {' '}
            </span>
            <div className='leading-tight text-white'>
              Interação é nosso nome do meio.
            </div>
          </div>
          <div
            className='
              text-white
              text-sm
              sm:text-base
              md:text-base
              lg:text-base
              xl:text-xl
              2xl:text-5xl
              3xl:text-4xl
              4xl:text-7xl
              pt-5'
              >
            <span >
              Apoiamos a presença online dos nossos clientes e implementamos
              habilmente estratégicas para lidar com isso. Somos a resposta tanto
              para as necessidades individuais médicas, quanto para estruturas
              completas. O WebPACS irá te ajudar a lidar com variadas demandas de
              exames e satisfazer o paciente com resultados onde desejar.
            </span>
          </div>
        </div>
        <div className="md:order-first xl:w-1/2 pt-6">
          <img
            className="object-center w-full"
            src={Image}
            alt="Exams Image"
          />
        </div>
    </div>
  )
}

export default Responsabilities
